<template>
  <nav class="navbar">
    <!-- Export button aligned to the left -->
    <button class="export-button" @click="exportToPNG">Export</button>

    <!-- Centered title -->
    <h1>Character Creator</h1>

    <!-- Navigation links aligned to the right -->
    <div class="nav-links">
      <router-link to="/">Builder</router-link>
      <router-link to="/add-part">Add Part</router-link>
      <router-link to="/find-parts">Find Parts</router-link>
    </div>
  </nav>
</template>

<script>
export default {
  name: 'Navbar',
  methods: {
    async exportToPNG() {
      try {
        // Get the character display area
        const characterBuilder = document.querySelector('.character-display');
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');

        // Set canvas size to match the character display
        canvas.width = characterBuilder.offsetWidth;
        canvas.height = characterBuilder.offsetHeight;

        // Helper function to draw images on canvas
        const drawImage = async (imgSrc, x = 0, y = 0) => {
          return new Promise((resolve, reject) => {
            const img = new Image();
            img.crossOrigin = "anonymous"; // Handle cross-origin issues
            img.src = imgSrc;
            img.onload = () => {
              ctx.drawImage(img, x, y, canvas.width, canvas.height);
              resolve();
            };
            img.onerror = reject;
          });
        };

        // List of layers in correct order
        const layers = [
          '.background-layer',
          '.hair-back-layer',
          '.jacket-back-layer',
          '.body-layer',
          '.shirt-layer',
          '.jacket-layer',
          '.ear-layer',
          '.head-layer',
          '.mouth-layer',
          '.nose-layer',
          '.eye-layer',
          '.eyebrow-layer',
          '.hair-layer'
        ];

        // Loop through each layer and draw on the canvas
        for (const layer of layers) {
          const imgElement = characterBuilder.querySelector(layer);
          if (imgElement) {
            await drawImage(imgElement.src);
          }
        }

        // Convert canvas to PNG and trigger download
        const dataURL = canvas.toDataURL('image/png');
        const link = document.createElement('a');
        link.href = dataURL;
        link.download = 'character.png';
        link.click();
      } catch (error) {
        console.error('Error exporting character:', error);
      }
    }
  }
};
</script>

