<template>
  <div class="edit-part-wrapper">
    <div class="edit-part">
      <!-- Image preview on the left -->
      <div class="image-container">
        <img :src="part.filePath" alt="Part Image" class="part-image" />
      </div>
      
      <!-- Form on the right -->
      <div class="form-container">
        <h3>Edit Part</h3>
        <form @submit.prevent="savePart">
          <!-- Character Name Field -->
          <div class="form-group">
            <label for="characterName">Character Name (optional)</label>
            <input type="text" v-model="part.characterName" placeholder="Enter character name" />
          </div>

          <!-- Part Name Field -->
          <div class="form-group">
            <label for="name">Part Name</label>
            <input type="text" v-model="part.name" placeholder="Enter part name" required />
          </div>

          <!-- Style Field (Optional) -->
          <div class="form-group">
            <label for="style">Style (optional)</label>
            <input type="text" v-model="part.style" placeholder="Enter style" />
          </div>

          <!-- Type Field -->
          <div class="form-group">
            <label for="type">Type (e.g., head, eyes, etc.)</label>
            <input type="text" v-model="part.type" placeholder="Enter part type" required />
          </div>

          <!-- "Has Back Part" Checkbox for Hair/Jacket (placed directly under Type) -->
          <div v-if="part.type === 'hair' || part.type === 'jacket'" class="form-group">
            <label>
              <input type="checkbox" v-model="part.hasBackPart" /> Has Back Part
            </label>
          </div>

          <!-- File Path Field -->
          <div class="form-group">
            <label for="filePath">File Path</label>
            <input type="text" v-model="part.filePath" placeholder="Enter file path" required />
          </div>

          <!-- Back Part File Path Field (Only visible when "Has Back Part" is checked) -->
          <div v-if="part.hasBackPart" class="form-group">
            <label for="backPartPath">Back Part File Path</label>
            <input type="text" v-model="part.backPartPath" placeholder="Enter back part file path" />
          </div>

          <!-- Noun Field with Radio Buttons -->
          <div class="form-group">
            <label for="noun">Noun</label>
            <div class="radio-group">
              <label>
                <input type="radio" v-model="part.noun" value="Person" /> Person
              </label>
              <label>
                <input type="radio" v-model="part.noun" value="Place" /> Place
              </label>
              <label>
                <input type="radio" v-model="part.noun" value="Thing" /> Thing
              </label>
            </div>
          </div>

          <!-- Tags Field -->
          <div class="form-group">
            <label for="tags">Tags (comma-separated)</label>
            <input type="text" v-model="tagsInput" placeholder="Enter tags" />
          </div>

          <!-- Save Part Button -->
          <button type="submit">Save Part</button>

          <!-- Delete Part Button -->
          <button type="button" @click="confirmDelete" class="delete-button">Delete Part</button>
        </form>
      </div>
    </div>

    <!-- Confirmation Popup for Deletion -->
    <div v-if="showDeletePopup" class="delete-popup-overlay">
      <div class="delete-popup">
        <p>Are you sure you want to delete this part?</p>
        <div class="popup-buttons">
          <button @click="deletePart">Yes</button>
          <button @click="closeDeletePopup">No</button>
        </div>
      </div>
    </div>

    <!-- Success message overlay for Save and Delete -->
    <AppOverlay :visible="saved" message="Saved!" />
    <AppOverlay :visible="deleted" message="Deleted!" />
  </div>
</template>

<script>
import { onValue, ref, remove, update } from "firebase/database";
import { database } from "../firebaseConfig";
import AppOverlay from "./AppOverlay.vue";

export default {
  components: {
    AppOverlay,
  },
  data() {
    return {
      part: {
        characterName: '',
        name: '',
        style: '',
        type: '',
        noun: '',
        filePath: '',
        hasBackPart: false,
        backPartPath: '',
        tags: [],
      },
      tagsInput: '',
      showDeletePopup: false,
      saved: false,
      deleted: false,
    };
  },
  methods: {
    fetchPart(name) {
      const partsRef = ref(database, 'parts');
      onValue(partsRef, (snapshot) => {
        const partsData = snapshot.val();
        const part = Object.values(partsData).find(p => p.name === name);
        if (part) {
          this.part = part;
          this.tagsInput = this.part.tags.join(', ');
        } else {
          console.error("Part not found");
        }
      });
    },
    savePart() {
      if (this.part) {
        this.part.tags = this.tagsInput.split(',').map(tag => tag.trim());
        const partRef = ref(database, `parts/${this.part.id}`);
        update(partRef, this.part)
          .then(() => {
            this.saved = true;
            setTimeout(() => {
              this.saved = false;
            }, 2000);
          })
          .catch(error => console.error('Error updating part:', error));
      }
    },
    confirmDelete() {
      this.showDeletePopup = true;
    },
    closeDeletePopup() {
      this.showDeletePopup = false;
    },
    deletePart() {
      const partRef = ref(database, `parts/${this.part.id}`);
      remove(partRef)
        .then(() => {
          this.deleted = true;
          setTimeout(() => {
            this.deleted = false;
            this.$router.push('/find-parts');
          }, 2000);
        })
        .catch(error => console.error('Error deleting part:', error));
    }
  },
  mounted() {
    const partName = this.$route.params.name;
    if (partName) {
      this.fetchPart(partName);
    } else {
      console.error('Part name is missing from route params');
    }
  }
};
</script>

