<template>
  <div class="character-builder-wrapper">
    <div class="clear-all">
      <!-- Clear All button beside the image -->
      <button @click="clearAll" class="clear-button">Clear All</button>
    </div>

    <div class="character-builder">
      <div class="character-display">
        <!-- Correct order from bottom to top -->
        <img v-if="selectedBackground" :src="selectedBackground" alt="Background" class="background-layer" />
        <img v-if="selectedHairBack" :src="selectedHairBack" alt="Hair Back" class="hair-back-layer" />
        <img v-if="selectedJacketBack" :src="selectedJacketBack" alt="Jacket Back" class="jacket-back-layer" />
        <img v-if="selectedBody" :src="selectedBody" alt="Body" class="body-layer" />
        <img v-if="selectedShirt" :src="selectedShirt" alt="Shirt" class="shirt-layer" />
        <img v-if="selectedJacket" :src="selectedJacket" alt="Jacket" class="jacket-layer" />
        <img v-if="selectedEar" :src="selectedEar" alt="Ear" class="ear-layer" />
        <img v-if="selectedHead" :src="selectedHead" alt="Head" class="head-layer" />
        <img v-if="selectedMouth" :src="selectedMouth" alt="Mouth" class="mouth-layer" />
        <img v-if="selectedNose" :src="selectedNose" alt="Nose" class="nose-layer" />
        <img v-if="selectedEye" :src="selectedEye" alt="Eye" class="eye-layer" />
        <img v-if="selectedEyebrow" :src="selectedEyebrow" alt="Eyebrow" class="eyebrow-layer" />
        <img v-if="selectedHair" :src="selectedHair" alt="Hair" class="hair-layer" />
      </div>

      <div class="side-buttons">
        <button @click="openMenu('head')" class="button">Select Head</button>
        <button @click="openMenu('nose')" class="button">Select Nose</button>
        <button @click="openMenu('ear')" class="button">Select Ear</button>
        <button @click="openMenu('eyebrow')" class="button">Select Eyebrow</button>
        <button @click="openMenu('eye')" class="button">Select Eye</button>
        <button @click="openMenu('mouth')" class="button">Select Mouth</button>
        <button @click="openMenu('hair')" class="button">Select Hair</button> <!-- Single button for both front/back -->
        <button @click="openMenu('shirt')" class="button">Select Shirt</button>
        <button @click="openMenu('jacket')" class="button">Select Jacket</button>
        <button @click="openMenu('background')" class="button">Select Background</button>
        <button @click="openMenu('body')" class="button">Select Body</button>
      </div>
    </div>

    <SlidingMenu v-if="menuOpen" :is-open="menuOpen" :part="selectedPart" @close="menuOpen = false" @part-selected="setSelectedPart" />
  </div>
</template>

<script>
import SlidingMenu from './SlidingMenu.vue';

export default {
  components: {
    SlidingMenu,
  },
  data() {
    return {
      menuOpen: false,
      selectedPart: null,
      selectedHead: null,
      selectedNose: null,
      selectedEar: null,
      selectedEyebrow: null,
      selectedEye: null,
      selectedMouth: null,
      selectedHair: null,
      selectedHairBack: null, // Separate for hair back
      selectedJacket: null,
      selectedJacketBack: null, // New jacket back part
      selectedBody: null,     // New body part
      selectedShirt: null,
      selectedBackground: null,
    };
  },
  methods: {
    openMenu(part) {
      this.menuOpen = true;
      this.selectedPart = part;
    },
    setSelectedPart(filePath, type, hasBackPart = false, backPartPath = null) {
      if (filePath === 'none') {
        filePath = this.getBlankSvg();
      }

      if (this.selectedPart === 'head') {
        this.selectedHead = filePath;
      } else if (this.selectedPart === 'nose') {
        this.selectedNose = filePath;
      } else if (this.selectedPart === 'ear') {
        this.selectedEar = filePath;
      } else if (this.selectedPart === 'eyebrow') {
        this.selectedEyebrow = filePath;
      } else if (this.selectedPart === 'eye') {
        this.selectedEye = filePath;
      } else if (this.selectedPart === 'mouth') {
        this.selectedMouth = filePath;
      } else if (this.selectedPart === 'hair') {
        this.selectedHair = filePath;
        if (hasBackPart && backPartPath) {
          this.selectedHairBack = backPartPath;
        } else {
          this.selectedHairBack = null; // Clear if no back part exists
        }
      } else if (this.selectedPart === 'jacket') {
        this.selectedJacket = filePath;
        if (hasBackPart && backPartPath) {
          this.selectedJacketBack = backPartPath;
        } else {
          this.selectedJacketBack = null; // Clear if no back part exists
        }
      } else if (this.selectedPart === 'body') {
        this.selectedBody = filePath;
      } else if (this.selectedPart === 'shirt') {
        this.selectedShirt = filePath;
      } else if (this.selectedPart === 'background') {
        this.selectedBackground = filePath;
      }

      this.saveToLocalStorage();
    },
    clearAll() {
      this.selectedHead = null;
      this.selectedNose = null;
      this.selectedEar = null;
      this.selectedEyebrow = null;
      this.selectedEye = null;
      this.selectedMouth = null;
      this.selectedHair = null;
      this.selectedHairBack = null; // Clear hair back
      this.selectedJacket = null;
      this.selectedJacketBack = null; // Clear jacket back
      this.selectedBody = null;     // Clear body
      this.selectedShirt = null;
      this.selectedBackground = null;

      this.saveToLocalStorage();
    },
    saveToLocalStorage() {
      const savedParts = {
        selectedHead: this.selectedHead,
        selectedNose: this.selectedNose,
        selectedEar: this.selectedEar,
        selectedEyebrow: this.selectedEyebrow,
        selectedEye: this.selectedEye,
        selectedMouth: this.selectedMouth,
        selectedHair: this.selectedHair,
        selectedHairBack: this.selectedHairBack, // Save hair back
        selectedJacket: this.selectedJacket,
        selectedJacketBack: this.selectedJacketBack, // Save jacket back
        selectedBody: this.selectedBody,         // Save body
        selectedShirt: this.selectedShirt,
        selectedBackground: this.selectedBackground,
      };
      localStorage.setItem('characterParts', JSON.stringify(savedParts));
    },
    loadFromLocalStorage() {
      const savedParts = JSON.parse(localStorage.getItem('characterParts'));
      if (savedParts) {
        this.selectedHead = savedParts.selectedHead;
        this.selectedNose = savedParts.selectedNose;
        this.selectedEar = savedParts.selectedEar;
        this.selectedEyebrow = savedParts.selectedEyebrow;
        this.selectedEye = savedParts.selectedEye;
        this.selectedMouth = savedParts.selectedMouth;
        this.selectedHair = savedParts.selectedHair;
        this.selectedHairBack = savedParts.selectedHairBack; // Load hair back
        this.selectedJacket = savedParts.selectedJacket;
        this.selectedJacketBack = savedParts.selectedJacketBack; // Load jacket back
        this.selectedBody = savedParts.selectedBody;         // Load body
        this.selectedShirt = savedParts.selectedShirt;
        this.selectedBackground = savedParts.selectedBackground;
      }
    },
    getBlankSvg() {
      return 'data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTAwIiBoZWlnaHQ9IjEwMCIgdmlld0JveD0iMCAwIDEwMCAxMDAiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+PHJlY3Qgd2lkdGg9IjEwMCIgaGVpZ2h0PSIxMDAiIGZpbGw9Im5vbmUiIHN0cm9rZT0ibm9uZSIvPjwvc3ZnPg==';
    }
  },
  mounted() {
    this.loadFromLocalStorage();
  },
};
</script>

<style scoped>
