<template>
  <div class="find-parts-wrapper">
    <div class="find-parts-container">
      <input
        type="text"
        class="search-bar"
        v-model="searchQuery"
        @input="filterParts"
        placeholder="Search for parts by name or tags..."
      />

      <div v-if="filteredParts.length > 0" class="masonry-grid">
        <div
          v-for="part in filteredParts"
          :key="part.id"
          class="card"
        >
          <img :src="part.filePath" :alt="part.name" class="part-image" />
          <div class="card-footer">
            <p>{{ part.name }}</p>
            <button @click="viewPartDetails(part)">Info</button>
          </div>
        </div>
      </div>

      <!-- Show message when no parts are found or no search query yet -->
      <p v-else>No parts found. Please enter a search term.</p>
    </div>
  </div>
</template>

<script>
import { onValue, ref } from "firebase/database";
import { database } from "../firebaseConfig";

export default {
  data() {
    return {
      parts: [],
      filteredParts: [],
      searchQuery: "",
    };
  },
  methods: {
    fetchParts() {
      const partsRef = ref(database, "parts");
      onValue(partsRef, (snapshot) => {
        const data = snapshot.val();
        if (data) {
          this.parts = Object.values(data);
        } else {
          this.parts = [];
        }
        this.filteredParts = []; // Empty filtered parts initially
      });
    },
    filterParts() {
      if (this.searchQuery.trim() === "") {
        this.filteredParts = [];
      } else {
        const query = this.searchQuery.toLowerCase();
        this.filteredParts = this.parts.filter((part) =>
          part.name.toLowerCase().includes(query) ||
          (part.tags && part.tags.some((tag) => tag.toLowerCase().includes(query)))
        );
      }
    },
    viewPartDetails(part) {
      this.$router.push({ name: "EditPart", params: { name: part.name } });
    }
  },
  mounted() {
    this.fetchParts();
  }
};
</script>

