import { createRouter, createWebHashHistory } from 'vue-router';
import AddPart from '../components/AddPart.vue';
import CharacterBuilder from '../components/CharacterBuilder.vue';
import EditPart from '../components/EditPart.vue';
import FindParts from '../components/FindParts.vue';

const routes = [
  { path: '/', component: CharacterBuilder },
  { path: '/add-part', component: AddPart },
  { path: '/find-parts', component: FindParts },
  { path: '/edit-part/:name', name: 'EditPart', component: EditPart },
];

const router = createRouter({
  history: createWebHashHistory('/character_creator/'), // Match with vue.config.js
  routes,
});

export default router;
