<template>
  <div :class="['sliding-menu', { 'is-open': isOpen }]">
    <button class="close-button" @click="closeMenu">X</button>
    <h3>Select a {{ part }}</h3>

    <!-- None button -->
    <button @click="selectNone" class="none-button">None</button>

    <div v-if="thumbnails.length > 0" class="thumbnail-grid">
      <div
        v-for="option in thumbnails"
        :key="option.id"
        class="thumbnail-container"
        @click="selectThumbnail(option)"
      >
        <!-- Hair handling for both front and back -->
        <div v-if="option.hasBackPart" class="hair-thumbnail">
          <img :src="option.backPartPath" alt="Hair Back" class="hair-back-layer" />
          <img :src="option.filePath" alt="Hair Front" class="hair-front-layer" />
        </div>
        <!-- Normal thumbnails for other parts -->
        <img v-else :src="option.filePath" :alt="option.name" class="thumbnail-card" />
        <p>{{ option.name }}</p>
      </div>
    </div>
    <p v-else>No images found for this part type.</p>
  </div>
</template>

<script>
import { onValue, ref } from "firebase/database";
import { database } from "../firebaseConfig";

export default {
  props: ['isOpen', 'part'],
  data() {
    return {
      thumbnails: [], // Holds the fetched thumbnails
    };
  },
  methods: {
    closeMenu() {
      this.$emit('close');
    },
    selectThumbnail(option) {
      this.$emit('part-selected', option.filePath, this.part, option.hasBackPart, option.backPartPath); // Emit selected part
    },
    selectNone() {
      this.$emit('part-selected', 'none', this.part); // Emit 'none' for removal
    },
    fetchThumbnails() {
      const partsRef = ref(database, 'parts');
      onValue(partsRef, (snapshot) => {
        const data = snapshot.val();
        if (data) {
          // Filter parts based on the selected part type (handle pluralization and case-insensitive comparison)
          this.thumbnails = Object.values(data).filter(part => {
            const type = part.type.toLowerCase();
            return (
              type === this.part.toLowerCase() ||
              type === `${this.part.toLowerCase()}s`
            );
          });
        } else {
          console.warn(`No parts found for type: ${this.part}`);
        }
      }, (error) => {
        console.error("Error fetching parts:", error);
      });
    }
  },
  watch: {
    part(newPart) {
      if (newPart) {
        this.fetchThumbnails(); // Fetch thumbnails whenever the part changes
      }
    }
  },
  mounted() {
    this.fetchThumbnails(); // Initial fetch when component is mounted
  }
};
</script>

<style scoped>
