  position: fixed;
  top: 0;
  right: -300px;
  width: 300px;
  height: 100vh;
  background-color: #333;
  color: white;
  z-index: 1000;
  transition: right 0.5s ease;
  padding: 20px;
}

.sliding-menu.is-open {
  right: 0;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: 1px solid white;
  color: white;
  font-size: 20px;
  cursor: pointer;
}

.thumbnail-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  grid-gap: 10px;
}

.thumbnail-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #696969; 
  padding: 10px;
  border-radius: 5px !important;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.hair-thumbnail {
  position: relative;
  width: 100px;
  height: 100px;
}

.hair-back-layer {
  position: absolute;
  z-index: 1;
  width: 100px;
  height: 100px;
  object-fit: contain;
}

.hair-front-layer {
  position: absolute;
  z-index: 2;
  width: 100px;
  height: 100px;
  object-fit: contain;
}

.thumbnail-card {
  width: 100%;
  height: auto;
  cursor: pointer;
  object-fit: contain; 
  border-radius: 0;
}

</style>
